"use client"
import React, { useState } from 'react'

export default function NewTask() {
  const defaultValues = {
      title: '',
      description: ''
  }

  const [values, setValues] = useState(defaultValues)

  const handleSubmit = (e) => {
    e.preventDefault()
    const title = e.target.title.value
    const description = e.target.description.value
    setValues({title, description})
    console.log(values)
  }
  return (
    <>
      <div className=' container mx-auto w-1/3 mt-15 shadow-xl'>
          <form 
            className=' bg-white h-screen flex flex-col items-center space-y-10 p-10 rounded-xl '
            onSubmit={handleSubmit}>
            <h1 className=' text-center text-3xl font-bold'>LLene el siguiente formulario para registrar un nuevo cliente</h1>
              <label htmlFor="title"
                className=' m-1 self-start font-bold text-md'
              >Nombre del cliente</label>
              <input 
                  type="text"
                  id='title'
                  placeholder='Nombre' 
                  className=' bg-slate-100 border border-slate-500 w-full rounded-sm p-2 text-black'
              />
              <label htmlFor="description"
                className=' m-1 self-start font-bold text-md'
              >Descripcion o datos del cliente del cliente</label>
              <textarea
                 name="" id="description" rows="3"
                 className='  bg-slate-100 border border-slate-500 w-full rounded-sm p-2'
                 placeholder='Descripcion'   
              ></textarea>
              <button 
                type='submit'
                className=' bg-sky-500 border hover:bg-sky-600 cursor-pointer border-slate-500 w-full p rounded-lg text-white font-bold text-xl p-2 text-black'
              >Registrar Cliente</button>
          </form>
      </div>
        
    </>
    
  )
}
